/* eslint-disable */
import ng from 'vendor-bb-angular';

import bbStorageModuleKey, { bbStorageServiceKey } from 'lib-bb-storage-ng';
import paymentOrdersData from './data-bb-payment-orders-http';

const paymentOrdersDataModuleKey = 'data-bb-payment-orders-http-ng';

export const paymentOrdersDataKey = 'data-bb-payment-orders-http-ng:paymentOrdersData';

export default ng
  .module(paymentOrdersDataModuleKey, [
    bbStorageModuleKey,
  ])
  .provider(paymentOrdersDataKey, [() => {
    const config = {
      baseUri: '/',
    };

    return {
      setBaseUri: (baseUri) => {
        config.baseUri = baseUri;
      },
      $get: [
        '$q',
        bbStorageServiceKey,
        '$httpParamSerializer',
        /* into */
       paymentOrdersData(config),
      ],
    };
  }])
  .name;



